<template>
    <div class="c_power">
        <CContentHeader :isShowIcon="false" content="权限管理" @back="click" />
        <div class="c_tab">
            <!-- Tabs 标签页插件 -->
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="角色管理" name="first">
                    <div class="c_tab_table">
                        <router-link to="/powerCreateRoles" class="c_tab_table_button">
                            <i class="el-icon-plus"></i> 新建角色
                        </router-link>

                        <!-- table表格插件 -->
                        <el-table :data="roleList" style="width: 100%">
                            <el-table-column width="450" prop="_name" label="名称"></el-table-column>
                            <el-table-column min-width="230" prop="alias" label="别名"></el-table-column>
                            <el-table-column width="250" label="操作">
                                <template slot-scope="scope">
                                    <el-button class="c_scope_but_configure" type="primary" @click="dialog(scope.row)" plain>权限配置</el-button>
                                    <el-button class="c_scope_but_revise" @click="roleRevise(scope.row)" type="text" size="small">修改</el-button>
                                    <el-button class="c_scope_but_delete" @click="roleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>

                        <!-- 分页 -->
                        <div class="c_pagination">
                            <div class="c_pagination_text">
                                共<span>{{pageCount}}</span>页
                            </div>
                            <div class="c_pagination_but">
                                <el-pagination @current-change="handleCurrentChange" :current-page.sync="pageIndex" :page-size="pageSize" layout="prev, pager, next, jumper" :total="pageTotal">
                                </el-pagination>
                            </div>
                        </div>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
        <!-- 弹框组件 -->
        <div class="c_elastic_frame">
            <!-- <PowerDeploy title="权限管理" :dialogShow.sync="dialogShow" @submit="submitBtn" @reset="dialogShow = false" ></PowerDeploy> -->
            <MaskLayer v-if="dialogShow" title="权限管理" twoTitle="用户将拥有以下权限" :dialogShow.sync="dialogShow" :powerList="powerList" @submit="submitBtn" @reset="resetBtn"></MaskLayer>

            <!-- @cancel="cancelBtn"  :menuList="menuList" -->
        </div>

    </div>
</template>

<script>
    //引入弹框
    import MaskLayer from "../../components/c_maskLayer.vue"

    export default {
        data() {
            return {
                activeName: "first", //tab栏显示的名称
                roleList: [{
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    },
                    {
                        _name: '王小虎',
                        alias: 'root'
                    }
                ], //表格数据
                pageIndex: 1, //当前页数
                pageSize: 10, //每页的条数
                pageTotal: 100, //数据的总数
                pageCount: 10, //数据的总页数 

                dialogShow: false, //是否显示遮罩层
                powerList: [],
                role_id: '', //角色id
                company_id: 'cp_001', //企业id   
                user_id: 'user_001',
            };
        },
        methods: {
            async submitBtn(obj) { //接收子组件传的参数  提交按钮
                this.dialogShow = false;
                console.log('接收到了');
                console.log(obj);
                let res = {
                    statusCode: 75500
                };


                if (obj.deleteList.length > 0) { //判断是否要删除权限
                    console.log('1111');
                    let item = obj.deleteList;
                    let isok = true;
                    let deleteObj = this.extract(item, isok);
                    res = this.unbindRoleOfTeamsAndUsers(deleteObj)

                }
                if (!obj.selectList.length > 0) {
                    if (res.statusCode === 75200) {
                        this.$zero.ToastSuccess("修改权限成功");
                    } else {
                        this.$zero.ToastWarning("服务异常，请稍后再试");
                    }
                    return;
                }
                let item = obj.selectList;
                let selectObj = this.extract(item);
                // return;
                let resTeam = {
                    statusCode: 75500
                };
                let resUser = {
                    statusCode: 75500
                };

                if (selectObj.teamList.length > 0) { //添加角色与部门的相关权限
                    resTeam = await this.$api.authorized.addTeamandrole({ //根据选择好的权限id，发送请求
                        company_id: this.company_id,
                        teams: selectObj.teamList,
                        role_id: this.role_id
                    })

                }

                if (selectObj.userList.length > 0) { //添加角色与用户的相关权限
                    resUser = await this.$api.authorized.addUserandrole({ //根据选择好的权限id，发送请求
                        company_id: this.company_id,
                        users: selectObj.userList,
                        role_id: this.role_id
                    })

                }

                // let res = await this.$api.authorized.addMenu({ //根据选择好的权限id，发送请求
                //     company_id: this.company_id,
                //     menus: this.menuList,
                //     role_id: this.role_id
                // })

                console.log(resTeam);
                console.log(resUser);
                if (resTeam.statusCode == 75200 || resUser.statusCode == 75200) {
                    this.$zero.ToastSuccess("修改权限成功");
                } else {
                    this.$zero.ToastWarning("服务异常，请稍后再试");
                }



            },
            extract(item, isok) { //数据处理
                let teamList = []; //选择权限部门id
                let userList = []; //选择权限用户id
                let teamNum = 0;
                let userNum = 0;
                // return;
                for (let i = 0; i < item.length; i++) { //遍历选择的权限，把选择好的id放到另外个数组中
                    // this.menuList.push(item[i].depid)
                    if (item[i].type == "dep") {
                        if (isok) {
                            teamList.push(item[i].depid)
                        } else {
                            teamList[teamNum] = {
                                id: item[i].depid,
                                name: item[i].name
                            }
                        }

                        teamNum++;
                    } else {
                        if (isok) {
                            userList.push(item[i].depid)
                        } else {
                            userList[userNum] = {
                                id: item[i].depid,
                                name: item[i].name
                            }
                        }


                        userNum++;
                    }
                }
                return {
                    teamList,
                    userList
                }
            },
            resetBtn() { //重置按钮
                // this.powerList = [];
                this.dialogShow = false;
            },

            click() {
                // console.log('父组件触发函数');
            },
            // comDialogVisible(){
            //     return this.dialogVisible;
            // },
            handleClick(tab, event) { //标签页事件
                console.log(111);

                console.log(tab, event);
            },
            roleRevise(row) { //表格修改按钮事件
                console.log('修改成功。。。');
                console.log(row);
                this.$router.push({
                    name: 'powerCreateRoles',
                    params: {
                        id: row._id
                    }
                })
            },
            async roleDelete(row, index) { //表格删除按钮事件
                console.log('删除成功。。。');
                console.log(row);
                // console.log(rows);
                console.log(index);


                let res = await this.$zero.DeleteTips('此操作将永久删除该角色, 是否继续?')
                console.log(res);
                if (res) {

                    // return;
                    // this.deleteRole(row._id);
                    let result = await this.$api.authorized.deleteRole({
                        company_id: this.company_id,
                        user_id: this.user_id,
                        role_id: row._id
                    })
                    if (result.statusCode === 75200) {
                        this.roleList;
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        this.roleList.splice(index, 1)
                        if (this.roleList.length == 0 && this.pageIndex != 1) {
                            console.log('我进来没');

                            this.pageIndex--
                        }
                        this.getRole();
                    } else {
                        this.$zero.ToastWarning(result.msg);
                    }
                }
                // return;
                // this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
                //     confirmButtonText: '确定',
                //     cancelButtonText: '取消',
                //     type: 'warning'
                // }).then(() => {

                //     this.$message({
                //         type: 'success',
                //         message: '删除成功!'
                //     });
                // }).catch(() => {
                //     this.$message({
                //         type: 'info',
                //         message: '已取消删除'
                //     });
                // });
            },
            // async deleteRole(roleid) {
            //     let result = await this.$api.authorized.deleteRole({
            //         company_id: this.company_id,
            //         user_id: this.user_id,
            //         role_id: roleid
            //     })
            //     if (result.statusCode === 75200) {
            //         this.roleList;
            //         this.$message({
            //             type: 'success',
            //             message: '删除成功!'
            //         });
            //         this.roleList.splice(index, 1)
            //         if (this.roleList.length == 0 && this.pageIndex != 1) {
            //             console.log('我进来没');

            //             this.pageIndex--
            //         }
            //         this.getRole();
            //     } else {
            //         this.$zero.ToastWarning(result.msg);
            //     }
            // },
            //当前页改变时的回调
            handleCurrentChange(val) {
                this.pageIndex = val;
                this.getRole();
                console.log(`当前页: ${val}`);

            },
            async dialog(row) { //权限配置按钮
                this.dialogShow = true;
                console.log(row);
                this.role_id = row._id
                // return;
                // this.powerList = [];
                let rolePowerList = []
                let res = await this.$api.authorized.getRolePower({ //获取已选权限
                    company_id: this.company_id,
                    user_id: this.user_id,
                    role_id: this.role_id
                })

                for (let i = 0; i < res.teams.length; i++) {
                    res.teams[i].type = 'dep'
                }
                for (let i = 0; i < res.users.length; i++) {
                    res.users[i].type = 'user'
                }


                rolePowerList = res.teams.concat(res.users)

                // console.log(rolePowerList);
                for (let i = 0; i < rolePowerList.length; i++) {
                    if (rolePowerList[i].type === 'dep') {
                        rolePowerList[i].name = rolePowerList[i].team_name;
                        rolePowerList[i].depid = rolePowerList[i].team_id;
                    } else {
                        rolePowerList[i].name = rolePowerList[i].user_name;
                        rolePowerList[i].depid = rolePowerList[i].user_id;
                    }

                }
                console.log(rolePowerList);
                this.powerList = JSON.parse(JSON.stringify(rolePowerList))




            },
            /**
             * 获取角色
             * @param {*} obj 
             * company_id {string} 公司ID *
             * user_id {string} 当前用户ID *
             * pageIndex {string} 当前页码 *
             * pageSize {string} 每页显示多少条 *
             */
            async getRole() {
                let result = await this.$api.authorized.getRole({
                    company_id: this.company_id,
                    user_id: this.user_id,
                    pageIndex: this.pageIndex,
                    pageSize: this.pageSize
                });
                // console.log(1111)
                // console.log(result);
                if (result.statusCode === 75200) {
                    this.pageTotal = result.total;
                    this.pageCount = Math.ceil(this.pageTotal / this.pageSize)
                    // console.log(this.pageCount);
                    // console.log(2222);
                    this.roleList = result.roles
                    this.scrollBar()
                } else if (result.statusCode === 75500) {
                    this.$zero.ToastWarning("服务异常，请稍后再试");
                } else {
                    this.$zero.ToastWarning("服务异常，请稍后再试");
                }
            },
            async unbindRoleOfTeamsAndUsers(obj) {
                let res = await this.$api.authorized.unbindRoleOfTeamsAndUsers({
                    company_id: this.company_id,
                    user_id: this.user_id,
                    role_id: this.role_id,
                    users: obj.userList,
                    teams: obj.teamList
                })
                return res;

            },
            scrollBar() { //滚动条样式
                $(".el-table__body-wrapper").getNiceScroll().remove();
                /**
                 * 设置滚动条滚动区域
                 * $('.el-table__body-wrapper')  需要滚动条的盒子
                 * niceScroll(".el-table__body",{})  需要滚动的内容盒子---滚动条盒子的子元素 {} 修改滚动条的参数详情百度搜索niceScroll
                 * cursorcolor    滚动条颜色
                 * cursorwidth    滚动条宽度
                 * autohidemode   滚动条是否隐藏
                 * background     滚动条背景颜色
                 * preventmultitouchscrolling    防止多触点事件引发滚动
                 */
                $('.el-table__body-wrapper').niceScroll(".el-table__body", {
                    cursorcolor: "#2f78ef",
                    cursorwidth: "5px",
                    autohidemode: false,
                    background: "#e6e6e6",
                    preventmultitouchscrolling: true,
                    bouncescroll: false,
                    // iframeautoresize: false,
                })
            }
        },
        mounted() {
            //获取角色
            this.getRole();
            // this.scrollBar()
        },
        beforeDestroy() {
            this.$zero.eventBus.$off("addBookLoadMore");
            this.$zero.eventBus.$off("departmentItem");
        },
        components: {
            MaskLayer //注册遮罩层组件
        }
    };
</script>

<style>
    .c_tab,
    .c_power {
        box-sizing: border-box;
        width: 100%;
    }

    /* tab栏样式 */
    .c_power .c_tab .el-tabs__nav-scroll {
        padding-left: 40px;
        background-color: #fff;
        padding-top: 5px;
    }

    .c_power .c_tab .el-tabs__nav-wrap::after {
        height: 0px;
    }

    .c_power .c_tab .el-tabs__active-bar {
        background-color: #5b94f2;
    }

    .c_power .c_tab .el-tabs__item.is-active {
        color: #141414;
    }

    .c_power .c_tab_table {
        box-sizing: border-box;
        width: 100%;
        padding: 0 40px;
    }

    /* 新建角色按钮样式 */
    .c_power .c_tab_table_button {
        display: inline-block;
        text-decoration: none;
        border-radius: 0px;
        margin-top: 15px;
        margin-bottom: 20px;
        padding: 10px 40px 10px 20px;
        background-color: #2f78ef;
        border-color: #2f78ef;
        color: #fff;
        font-size: 14px;
    }

    .c_power .c_tab_table .el-table th {
        padding: 8px 0px;
        color: #999;
    }

    .c_power .c_tab_table .el-table td {
        border-bottom: 1px solid #e1e1e1;
        color: #1f1f1f;
        padding: 0px;
    }

    /* 表格头下划线 */
    .c_power .c_tab_table .el-table th.is-leaf {
        border-bottom: 1px solid #d9d9d9;
    }

    /* 设置表格的右偏移 */
    .c_power .c_tab_table .el-table td:first-child,
    .c_power .c_tab_table .el-table th:first-child {
        padding-left: 40px;
    }

    /* 表格内按钮样式 */
    .c_scope_but_configure.is-plain {
        border-radius: 0px;
        padding: 10px 17px;
        margin-top: 15px;
        margin-bottom: 12px;
        font-size: 12px;
        background-color: #fff;
        border-color: #2f78ef;
        color: #2f78ef;
        margin-right: 20px;
    }

    .c_scope_but_configure.is-plain:hover {
        background-color: #2f78ef;
        border-color: #2f78ef;
        color: #fff;
    }

    .c_power .c_scope_but_revise,
    .c_power .c_scope_but_delete {
        position: relative;
        color: #707070;
    }

    .c_power .c_scope_but_revise:hover,
    .c_power .c_scope_but_delete:hover {
        color: #2f78ef;
    }

    .c_power .c_scope_but_revise::after {
        content: "";
        position: absolute;
        width: 1px;
        height: 12px;
        background-color: #707070;
        right: -6px;
        top: 9px;
    }

    /* 分页样式 */
    .c_power .c_pagination {
        padding: 0px 40px;
        height: 60px;
        border-top: 1px solid #d9d9d9;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .c_power .c_pagination_text {
        font-size: 14px;
        color: #707070;
    }

    /* 弹框模块 */
    /* .c_power .c_elastic_frame .el-dialog__header {
        border-bottom: 1px solid #d9d9d9;
        font-weight: bold;
    }

    .c_power .c_elastic_frame .el-dialog__body {
        padding-top: 15px;
        padding-bottom: 55px;
    }

    .c_power .c_elastic_frame .el-dialog__footer {
        border-top: 1px solid #d9d9d9;
        padding: 15px 20px;
    }

    .c_power .c_elastic_frame .el-input__inner {
        text-indent: 18px;
        border: 1px solid #e1e1e1;
    } */



    /* 固定表格高度 */
    .c_power .el-table__body-wrapper {
        height: 480px;
        overflow-y: auto;
    }

    /* 设置表格滚动条 */
    /* .el-table--scrollable-y .el-table__body-wrapper */
    /* .c_power .el-table__body-wrapper::-webkit-scrollbar {
        width: 6px;
        height: 10px;
        background-color: #e6e6e6;
    }

    .c_power .el-table__body-wrapper::-webkit-scrollbar-thumb {
        background-color: #2f78ef;
        height: 103px;
    } */
</style>